html, body {
  margin: 0;
  
  font-family: '나눔스퀘어라운드', sans-serif;
  font-size: 2.5vmin;
  color: hsl(42, 10%, 50%);
  line-height: 1.5;
}

#root {
  height: 100vh;
  width: 100vw;

  overflow-x: hidden;
  overflow-y: auto;

  scroll-behavior: smooth;
}



body, header, nav, article, section, footer, div, span, ul, li{
  position: relative;  box-sizing: border-box;
}

.position-center {
  position: absolute;
  bottom: 50%;
  right: 50%;
  -webkit-transform: translateX(50%) translateY(50%);
          transform: translateX(50%) translateY(50%);
}

.show-right { right: 0 }
.hidden-right { right: -100% }



.fade-in, .fade-out {
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.fade-in {-webkit-animation-delay: 0.5s;animation-delay: 0.5s}
.fade-out { animation-direction: reverse; }
@-webkit-keyframes fade { 0% { opacity: 0; } 100% { opacity: 1; } }
@keyframes fade { 0% { opacity: 0; } 100% { opacity: 1; } }



.display-none { display: none }



.button:hover, .close-button:hover { cursor: pointer }
.jamie-who {
  height: 100%;
  width: 100%;
}

.text-heading, h1 {
  font-size: 2rem;
  font-weight: 800;
  line-height: calc(1 + 1 / 3);
}

.text-right {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: right;
  margin-right: 0;
  margin-left: auto;
}
.text-left {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: left;
}
.text-center {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
  margin: 0 auto;
}



.jamie-who-logo{
  height: 8vmin;
  width: 8vmin;
  border-radius: 0 0 3vmin 0;
  background-color: #fab617;

  text-transform: lowercase;
  font-size: 4vmin;
  font-weight: 800;
  color: rgba(255,255,255,0.7);
  line-height: 1;

  display: grid;
  align-items: center;

  -webkit-transition: 0.5s color;

  transition: 0.5s color;
}
.jamie-who-logo:hover { color: rgba(255,255,255,1); }
.jamie-who-logo > span {
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  margin: auto;
}

.close-button {
  height: 6vmin;
  width: 6vmin;
}

.close-button::before, .close-button::after {
  content: "";
  padding-right: 6vmin;

  position: absolute;
  top: 50%;
  -webkit-transform-origin: center;
          transform-origin: center;
  border-top: 1vmin solid #fab617;
}

.close-button::before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg)
}
.close-button::after{
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg)
}

.outer-link {
  display: inline-block;
  opacity: 0.7;
  -webkit-transition: 0.5s opacity;
  transition: 0.5s opacity;

  width: 0.8em;
  height: 0.8em;

  background-image: url(/static/media/external-link.1ae83b0d.svg);
  background-size: contain;
}
.outer-link:hover { opacity: 1; }
.load-indicator{
  z-index: 100;
  border-radius: 1vmin;
}
.load-indicator-wrapper {
  display: grid;
  grid-auto-flow: row;
  justify-items: center;
}

.load-indicator-progress {
  width: 70vmin;
  height: 7vmin;

  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
}
.load-indicator-progress[value]::-webkit-progress-bar,
.load-indicator-progress[value]::-webkit-progress-value {border-radius: 5vmin;}

.load-indicator-progress[value]::-webkit-progress-bar {background-color: #eee;}
.load-indicator-progress[value]::-webkit-progress-value {background-color: #fab617; -webkit-transition: 100ms ease; transition: 100ms ease;}
.load-indicator-text {
  line-height: 100%;

  position: absolute;
  right: 50%;
  bottom: 50%;
  -webkit-transform: translateX(50%) translateY(50%);
          transform: translateX(50%) translateY(50%);
}
.jamie-who-header {
  position: fixed;
  top: 0;
  left: 0;

  -webkit-transition-duration: 1500ms;

          transition-duration: 1500ms;
  -webkit-transition-property: height, width;
  transition-property: height, width;
  z-index: 100;
}

.jamie-who-header-wrapper {
  position: absolute;
  -webkit-transition-duration: 1500ms;
          transition-duration: 1500ms;
  -webkit-transition-property: top, left, -webkit-transform;
  transition-property: top, left, -webkit-transform;
  transition-property: top, left, transform;
  transition-property: top, left, transform, -webkit-transform;
}
.header-normal .header-title {
  -webkit-transition-duration: 1500ms;
          transition-duration: 1500ms;
  -webkit-transition-property: background-color;
  transition-property: background-color;
}


.header-special { height: 100%; width: 100%; text-align: center;}
.header-special div{ width: -webkit-max-content; width: -moz-max-content; width: max-content; margin: auto;}

.header-special .jamie-who-header-wrapper {
  top: 50%;
  left: 50%;
  -webkit-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
}

.header-special .header-title::before,
.header-special .header-title::after {
  padding-left: 1vmin;
  padding-right: 1vmin;
  color: #fab617;
  font-size: 1.2em;
}
.header-special .header-title::before { content: "{"; }
.header-special .header-title::after { content: "}"; }

.header-special .header-dscrp {color: hsl(42,50%,75%); font-weight: bold}





.header-normal { height: 0; width: 0 }
.header-normal .jamie-who-header-wrapper {
  top: 1vmin;
  left: 2vmin;
}
.header-normal .header-dscrp { display: none; }



.header-nav {
  margin-top: 4vmin;
  width: 90vmin;

  margin: 0 auto;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}

.header-nav-list{
  list-style: none;
  margin: 0;
  padding: 0;

  display: grid;
  grid-template-columns:  1fr 1fr 1fr 1fr;
  justify-content: space-evenly;
  grid-gap: 2vmin;
}

.header-nav-item {
  font-size: 4vmin;
  font-weight: 800;
  border: 1vmin solid transparent;
  border-radius: 3vmin;
  color: hsl(42, 96%, 75%);

  -webkit-transition: 0.5s color, 0.5s border-bottom-color;

  transition: 0.5s color, 0.5s border-bottom-color;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}
.header-nav-item:hover { color: #fab617; border-bottom-color: #fab617}
.jamie-who-nav {
  position: fixed;
  top: 0;

  height: 100%;
  width: 0;

  z-index: 101;
}

.nav-wrapper {
  height: 100%;
  width: 40vmin;
  background-color: white;

  position: absolute;
  top: 0;
  left: -40vmin;

  -webkit-transition: 0.5s left;

  transition: 0.5s left;
}
.nav-show .nav-wrapper { left: 0}



.nav-logo { margin: 10vmin auto; }

.nav-close {
  position: absolute;
  bottom: 5vmin;
  right: 50%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
}

.nav-main-item, .nav-sub-item {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
}



.nav-main-contents,
.nav-sub-contents {
  list-style: none;
  margin: 0;
  padding: 0;

  font-size: 3vmin;
  text-align: center;
}
.nav-main-item { padding: 4vmin; -webkit-transition-property: background-color; transition-property: background-color; }
.nav-main-item:hover { background-color: #fab617 }
.nav-main-current { background-color: #fab617; }



.nav-sub-contents {
  position: absolute;
  top: 0;
  left: 40vmin;

  display: grid;
  align-items: stretch;

  height: 100%;
  width: 1vmin;

  background-color: #fab617;
  opacity: 0;
  -webkit-transition: 1.5s opacity;
  transition: 1.5s opacity;
}
.nav-indicate .nav-sub-contents { opacity: 1;}
.nav-show .nav-sub-contents { opacity: 1;}



.nav-sub-item {
  width: 1vmin;

  -webkit-transition-property: color, width, background-color;

  transition-property: color, width, background-color;
  
  color: transparent;
  font-weight: 800;
  /* font-size: 4vmin; */
  background-color: #fab617;
  white-space: nowrap;
}
.nav-sub-item:hover { background-color: #E30613; }
.nav-sub-current { background-color: #E30613; }
.nav-indicate .nav-sub-item:hover { width: 40vmin;  color: white }
.nav-show .nav-sub-item { width: 40vmin; color: rgba(255,255,255,0.9) }

.nav-sub-item .item-text {
  height: 100%;
  width: 100%;

  position: absolute;
  right: 40vmin;
  
  display : grid;
  align-items: center;
}
.nav-sub-item:hover .item-text,
.nav-show .nav-sub-item .item-text { right: 0; }
