/* outer container */

.jamie-who-description {
  height: 100%;
  width: 100%;

  display: flex;
  overflow-x: hidden;
  overflow-y: hidden;

  position: fixed;
  left: 0;

  -webkit-transition: 0.5s top ease;

  transition: 0.5s top ease;
  
  z-index: 101;
}
.description-hide { top: -100%; -webkit-animation: 0.5s opacity-to-zero; animation: 0.5s opacity-to-zero;}
.description-show { top: 0; -webkit-animation: 0.5s opacity-to-hundred; animation: 0.5s opacity-to-hundred}

@-webkit-keyframes opacity-to-zero {
  0% { opacity: 1;}
  100% { opacity: 0;}
}

@keyframes opacity-to-zero {
  0% { opacity: 1;}
  100% { opacity: 0;}
}
@-webkit-keyframes opacity-to-hundred {
  0% { opacity: 0;}
  100% { opacity: 1;}
}
@keyframes opacity-to-hundred {
  0% { opacity: 0;}
  100% { opacity: 1;}
}

.jamie-who-description::before {
  content: "";
  height: 300%;
  width: 300%;

  position: absolute;
  top: -100%;
  left: -100%;

  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(hsla(356, 95%, 75%, 0.75)),
    color-stop(hsla(42, 96%, 75%, 0.75)),
    to(hsla(356, 95%, 75%, 0.75))
  );

  background: -webkit-linear-gradient(
    top,
    hsla(356, 95%, 75%, 0.75),
    hsla(42, 96%, 75%, 0.75),
    hsla(356, 95%, 75%, 0.75) /* hsla(42, 96%, 54%, 1) */
  );

  background: linear-gradient(
    to bottom,
    hsla(356, 95%, 75%, 0.75),
    hsla(42, 96%, 75%, 0.75),
    hsla(356, 95%, 75%, 0.75) /* hsla(42, 96%, 54%, 1) */
  );
  background-size: 400% 400%;
  -webkit-animation: DescriptionBgGradient 20s linear infinite;
          animation: DescriptionBgGradient 20s linear infinite;
}

@-webkit-keyframes DescriptionBgGradient {
  0% {
    background-position: 50% 0%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% { background-position: 100% 50%; }
  50% { background-position: 50% 100%; }
  75% { background-position: 0% 50%; }
  100% {
    background-position: 50% 0%;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes DescriptionBgGradient {
  0% {
    background-position: 50% 0%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% { background-position: 100% 50%; }
  50% { background-position: 50% 100%; }
  75% { background-position: 0% 50%; }
  100% {
    background-position: 50% 0%;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



